/* eslint-disable no-unused-vars */
'use strict';

// MODULES //

import NINF from '@stdlib/constants/float64/ninf';
import isPrimitive from './primitive.js';
import isObject from './object.js';


// MAIN //

/**
* Tests if a value is a nonpositive finite number.
*
* @param {*} value - value to test
* @returns {boolean} boolean indicating if a value is non positive finite
*
* @example
* var bool = isNonPositiveFinite( -3.0 );
* // returns true
*
* @example
* var bool = isNonPositiveFinite( new Number( -3.0 ) );
* // returns true
*
* @example
* var bool = isNonPositiveFinite( -Infinity );
* // returns false
*/
function isNonPositiveFinite( value ) {
	return (
		isPrimitive(value) || isObject(value));
}


// EXPORTS //

export default isNonPositiveFinite;
