'use strict';

// MODULES //

import isAccessorArray from './../../is-accessor-array';
import hasSameValues from '@stdlib/array/base/assert/has-same-values';


// MAIN //

/**
* Tests if two arguments are both accessor arrays and have the same values.
*
* @param {*} v1 - first value
* @param {*} v2 - second value
* @returns {boolean} boolean result
*
* @example
* import Complex128Array from '@stdlib/array/complex128';
*
* var x = new Complex128Array( [ 1, 2, 1, 2 ] );
* var y = new Complex128Array( [ 1, 2, 1, 2 ] );
* var out = isSameAccessorArray( x, y );
* // returns true
*
* @example
* import Complex128Array from '@stdlib/array/complex128';
*
* var x = new Complex128Array( [ 1, 2, 1, 2 ] );
* var y = new Complex128Array( [ 2, 1, 2, 1 ] );
* var out = isSameAccessorArray( x, y );
* // returns false
*
* @example
* import Complex128Array from '@stdlib/array/complex128';
*
* var x = new Complex128Array( [ 1, 2, 1, 2 ] );
* var y = [ 1, 2, 3 ];
* var out = isSameAccessorArray( x, y );
* // returns false
*/
function isSameAccessorArray( v1, v2 ) {
	return ( isAccessorArray( v1 ) && isAccessorArray( v2 ) && hasSameValues( v1, v2 ) ); // eslint-disable-line max-len
}


// EXPORTS //

export default isSameAccessorArray;
